<template>
  <div class="share-container">
    <HeadTop :Headtype="Headtype" title="分享推广"></HeadTop>
    <div class="content">
      <div class="img-logo">
        <img src="@/assets/images/share_logo@2x.png" alt="">
      </div>
      <div class="share-top">
        <div class="address-tit">
          推广二维码
        </div>
        <div class="qrcode-box">
          <img :src="imgQrcode" alt="">
        </div>
        <div class="qr-des">长按识别上图二维码进行分享</div>
        <div class="split-line"></div>
        <div class="flex-column link-text">
          <div class="label">推广链接
            <!-- <img class="lab-img" src="@/assets/images/share-tit.png"> -->
          </div>
          <div class="cont flex-item">{{link}}</div>
        </div>
        <div class="share-btn">
          <van-button type="primary" class="clip-button" :data-clipboard-text="link">复制并分享</van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import VueQr from 'vue-qr'
import HeadTop from '@/components/HeadTop'
import { Toast } from 'vant';
import QRCode from 'qrcode'
import Clipboard from 'clipboard'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";
export default {
  components: {
    HeadTop
  },
  setup () {
    var clipboard = new Clipboard('.clip-button')
    clipboard.on('success', function (e) {
      Toast(t('share.复制成功'))
      e.clearSelection()
    })
    clipboard.on('error', function () {
      Toast(t('share.复制失败，请手动长按复制'))
    })
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: 'back',
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      freezeContract: null,
      windTokenContract: null,
      imgQrcode: '',
      reward: '',
      link: '',
      curBlock: '',
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.link = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        QRCode.toDataURL(data.link, { margin: 0.5, width: 160 })
          .then(url => {
            data.imgQrcode = url
          })
          .catch(err => {
            console.error(err)
          })

      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
.share-container {
  .footer {
    display: none;
  }
}
</style>
<style lang="less" scoped>
.share-container {
  min-height: calc(100vh - 46px);
  background: #3364f3 url(../../assets/images/share_bg@2x.jpg) no-repeat;
  background-size: 100% 100%;
}
.share-top {
  // background: url(~@/assets/images/code-bg.png) no-repeat;
  background: #fff;
  background-size: 100% auto;
  margin: -45px 37.5px 0;
  padding-top: 45px;
  border-radius: 10px;
  text-align: center;
  overflow: hidden;
  .address-tit {
    // background: url(~@/assets/images/wallettit-bg.png) no-repeat;
    background-size: 100% 100%;
    color: #fff;
    font-size: 17px;
    color: #333;
    font-weight: 700;
  }
  .address {
    padding: 10px 47px;
    word-wrap: break-word;
    color: #010101;
    font-size: 15px;
    font-weight: bold;
  }
  .qrcode-box {
    width: 166px;
    height: 166px;
    margin: 15px auto 18.5px;
    background: #fff;
    padding: 3px;
  }
  .link-text {
    font-size: 15px;
    .label {
      font-size: 17px;
      font-weight: bold;
      color: #333;
      position: relative;
      padding: 0 39px;
      .lab-img {
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 139px;
        height: 9px;
        top: 5px;
      }
    }
    .cont {
      margin: 22px 0 20px;
      text-align: center;
      word-break: break-all;
      font-size: 13px;
      color: #666666;
      line-height: 17px;
      padding: 15px 15px;
      width: 260px;
      background: #f1f1f1;
      border-radius: 8px;
    }
    .copy-icon {
      width: 13px;
      height: 13px;
      margin: 5px 0 0 14px;
    }
  }
  .share-btn {
    padding-bottom: 30px;
    .van-button--primary {
      font-size: 15px;
      height: 40px;
      width: 210px !important;
      border-radius: 20px !important;
    }
  }
}
.content {
  padding-top: 22px;
  padding-bottom: 72px;
  line-height: 1.3;
  .qr-des {
    font-size: 14px;
    color: #999;
  }
  .split-line {
    position: relative;
    border-top: 1px dotted #999999;
    margin: 27.5px 0;

    &::before {
      content: '';
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background: #3364f3;
      position: absolute;
      right: -7.5px;
      top: -7.5px;
    }
    &::after {
      content: '';
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background: #3364f3;
      position: absolute;
      left: -7.5px;
      top: -7.5px;
    }
  }
}
.img-logo {
  width: 129px;
  height: auto;
  position: relative;
  margin: 0 auto;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>
